@charset "utf-8";
/*==================================
  index.html
===================================*/
.ly_body_inner {
	position: static;
	@media screen and(min-width:#{$bp-pc}) {
		position: relative;
	}
}
.ly_secInner {
	width: calc(100% - 30px);
	margin: 0 auto;
	@media screen and(min-width:#{$bp-tab}) and(max-width: #{$bp-tabMx}) {
		width: calc(100% - 60px);
	}
}
.el_secTit {
	font-family: Montserrat;
	font-size: 1.75em;
	@media screen and(min-width:#{$bp-tab}) and(max-width: #{$bp-tabMx}) {
		font-size: 2em;
	}
	@media screen and(min-width:#{$bp-pc}) {
		font-size: 2em;
	}
	font-weight: bold;
	margin-bottom: 30px;
}
.el_btn {
	background-color: #000000;
	width: 160px;
	height: 36px;
	font-family: 'Montserrat Medium';
	font-size: .6875em;
	margin-top: 30px;
}
.el_borderB {
	border-bottom: 1px solid #ddd;
}
