/*-----------------
	about
-------------------*/
.ly_about {
	@include bgi("../img/about-img__sm.jpg");
	margin-bottom: 0;
	@media screen and(min-width:#{$bp-pc}) {
		margin: 0 170px;
	}
}
.ly_secInner.ly_secInner__about {
	padding: 50px 0;
	@media screen and(min-width:#{$bp-pc}) {
		margin: 0;
		padding: 110px 460px 110px 50px;
	}
}
.ly_aboutCopyCont {
	background-color: #ffffff;
	padding: 50px 15px 50px 30px;
	@media screen and(min-width:#{$bp-tab}) and(max-width: #{$bp-tabMx}) {
		padding: 40px 30px;
		margin: 0 60px;
	}
}
.el_copy.elcopy__about {
	font-size: .875em;
	line-height: 2;
	letter-spacing: 0;
}
.pc_background {
	position: relative;
}
.pc_background_inner {
	display: none;
	@media screen and(min-width:#{$bp-pc}) {
		display: block;
		height: 110px;
		width: 100%;
		top:-110px;
		background-color: #f0f0f0;
		position: absolute;
		z-index: -1;
	}
}
/*-----------------
	works
-------------------*/
.ly_works {
	@media screen and(min-width:#{$bp-pc}) {
		margin-right: 60px;
	}
}
.ly_works, .ly_culture {
	@media screen and(min-width:#{$bp-pc}) {
		margin-bottom: 0;
	}
}
.ly_secInner.ly_secInner_works,
.ly_secInner.ly_secInner_culture,
.ly_secInner.ly_secInner_topics {
	@media screen and(min-width:#{$bp-pc}) {
		width: 100%;
	}
}
.ly_sectionsWrapper {
	background-color: #f0f0f0;
	padding-top: 40px;
	display: block;
	@media screen and(min-width:#{$bp-pc}) {
		display: flex;
		padding: 80px 170px 0;
	}
}
.bl_card {
	position: relative;
	min-height: 232px;
}
.el_filter, .bl_card, .el_img__md {
	@media screen and(min-width:#{$bp-tab}) and(max-width: #{$bp-tabMx}) {
		min-width: 520px;
		min-height: 350px;
	}
	@media screen and(min-width:#{$bp-pc}) {
		min-width: 520px;
		min-height: 350px;
	}
}
.el {
	&_filter {
		position: absolute;
		z-index: #{$z-layer02};
		opacity: .24;
	}
	&_card_img {
		position: absolute;
		z-index: #{$z-layer01};
	}
}
/*-----------------
	culture
-------------------*/
.el {
	&_filter#{&}_filter__culture {
		opacity: .34;
	}
	&_btn#{&}_btn_culture {
		margin-bottom: 40px;
	}
}
/*-----------------
	topics
-------------------*/
.ly_topics {
	@media screen and(min-width:#{$bp-pc}) {
		margin: 0 170px;
	}
}
.ly_secInner.ly_secInner_topics {
	display: block;
	@media screen and(min-width:#{$bp-pc}) {
		padding: 60px 0;
		display: flex;
		flex-direction: column;
	}
}
.bl {
	&_topicsList {
		@media screen and(min-width:#{$bp-pc}) {
			margin-left: 400px;
		}
	}
	&_topicsList_item + #{&}_topicsList_item {
		margin-top: 22px;
	}
}
.el_secTit.el_secTit_topics {
	@media screen and(min-width:#{$bp-pc}) {
		margin-bottom: 55px;
	}
}
.el_topicsList{
	&_date {
		font-size: .625em;
		color: #cecece;
		margin-bottom: 11px;
	}
	&_detail {
		font-size: .75em;
		@media screen and(min-width:#{$bp-tab}) and(max-width: #{$bp-tabMx}) {
			font-size: .875em;
		}
		font-weight: bold;
		margin-bottom: 10px;
	}
}
/*-----------------
	contact
-------------------*/
.ly_contact {
	padding: 40px 0;
	margin-bottom: 0;
	background-color: #f2f2f2;
	@media screen and(min-width:#{$bp-tab}) and(max-width: #{$bp-tabMx}) {
		padding: 40px 0 80px 0;
	}
	@media screen and(min-width:#{$bp-pc}) {
		padding: 60px 0 120px 0;
	}
}
.ly_copybox {
	@media screen and(min-width:#{$bp-pc}) {
		width: 440px;
		order: -1;
		margin-right: 80px;
	}
}
.el_copy.el_copy_contact {
	line-height: 2;
	letter-spacing: 0;
}
.el_contact_img {
	@media screen and(min-width:#{$bp-pc}) {
		width: 580px;
		height: 390px;
	}
}
.el_btn.el_btn_topics {
	margin-top: 42px;
	margin-left: 0;
	@media screen and(min-width:#{$bp-pc}) {
		margin-left: auto;
	}
}