----------------------*/
.nav_drawer {
  position: relative;
}

.nav_unshown {
  display: none;
}

#nav_open {
  display: inline-block;
  width: 30px;
  height: 22px;
  vertical-align: middle;
}

#nav_open span, #nav_open span:before, #nav_open span:after {
  position: absolute;
  height: 2px;
  width: 22px;
  border-radius: 3px;
  background: #fff;
  display: block;
  content: '';
  cursor: pointer;
}

#nav_open span:before {
  bottom: -5px;
}

#nav_open span:after {
  bottom: -10px;
}

#nav_content {
  overflow: auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background: #222;
  transition: .3s ease-in-out;
  -webkit-transform: translateX(-105%);
  transform: translateX(-105%);
}

#nav_input:checked ~ #nav_close {
  display: block;
  opacity: .5;
}

#nav_input:checked ~ #nav_content {
  -webkit-transform: translateX(0%);
  transform: translateX(0%);
  box-shadow: 6px 0 25px rgba(0, 0, 0, 0.15);
}

.el_hamburgerClose {
  margin: 24px 8.5px 0 calc(100% - 75px);
  display: flex;
  width: 25px;
  height: 25px;
  position: relative;
  border: 2px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

.el_hamburgerClose span {
  margin-left: 25px;
}

.el_hamburgerClose span::before,
.el_hamburgerClose span::after {
  display: block;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 84%;
  height: 16%;
  margin: -8% 0 0 -42%;
  background: #fff;
}

.el_hamburgerClose span::before {
  transform: rotate(-30deg);
}

.el_hamburgerClose span::after {
  transform: rotate(30deg);
}

.bl_hamburger {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bl_hamburger_Wrapper {
  position: relative;
}

.el_hamburger_item {
  font-size: 1em;
  font-style: italic;
}

.el_hamburger_item + .el_hamburger_item {
  margin-top: 30px;
}

.el_hamburger_item.el_hamburger_badge {
  display: flex;
  justify-content: center;
}

